import React, { useState, useEffect } from 'react'
import axios from '../api/actions/axios';
import Header from '../components/Header'
import MostrarTotalAeropuertos from '../components/MostrarTotalAeropuertos';

const Aeropuertos = () => {

    const [data, setData] = useState();

    useEffect(() => {
        if(!data) {
            axios("aeropuertos").then( (res) => {
                if(res.status !== 200) {
                    alert(res.data.message);
                } else {
                    setData(res.data);
                }
            })
        }  
    })

    return (
        <>
            <Header/>
            <h2>Informacion sobre los aeropuertos</h2>
            <hr/>
            { data ? (
                <MostrarTotalAeropuertos data={data}/>
                ): null}
        </>
    )
}

export default Aeropuertos
