import React from 'react'

const FormRadio = ( { texto, id, value, checked = false, onChange } ) => {
    return (
        <div className="form-check form-check-inline"  >
            <input type="radio" className="form-check-input" name="inputTipo" id={ id } value={ value } checked={ checked } onChange={ (event) => { onChange( event.target.value ) }}/>
            <label htmlFor={ id } className="form-check-label" >{ texto } </label>
        </div>
    )
}

export default FormRadio
