import React, { useState, useEffect } from 'react'
import axios from '../api/actions/axios';
import Header from '../components/Header'
import MostrarTotalVuelos from '../components/MostrarTotalVuelos';

const TotalVuelos = () => {

    const [data, setData] = useState();

    useEffect(() => {
        if(!data) {
            axios("vuelos-pais").then( (res) => {
                if(res.status !== 200) {
                    alert(res.data.message);
                } else {
                    setData(res.data);
                }
            })
        }  
    })
    return (
        <>
            <Header/>
            <h2>Informacion sobre los vuelos por pais<br/> (Pais - Total de vuelos)</h2> <hr/>
            { data ? (
                <MostrarTotalVuelos data={data}/>
                ): null}
        </>
    )
}

export default TotalVuelos
