import React from 'react'

const MostrarTotalVuelos = ( data ) => {

    const datos = data.data.map(e => { return e })

    return (
        <>
            <h3 className=" text-center">
                <ul >
                    {datos.map(d => { return <li key={d.toString()}>Pais numero {d[0]} : <b>{ d[1] }</b> -&gt; { d[2] }</li>  })}
                </ul>
            </h3>
        </>
    )
}

export default MostrarTotalVuelos