import React, { useState } from 'react'
import axios from '../api/actions/axios';
import '../style.css';
import MostrarVuelo from './MostrarVuelo';

export const NumeroVuelo = () => {

    const [data, setData] = useState();
    const [numVuelo,  setNumVuelo] = useState('')
    
    const handleSubmit = (e) => {
        axios("vuelos", {"vuelo": numVuelo}).then( (res) => {
            if(res.status !== 200) {
                alert(res.data.message);
            } else {
                setData(res.data);
            }
        })
        e.preventDefault();
    }
    return (
        <>
            <h2 className="h2">Buscar un vuelo</h2> <hr/>
            <section className="text-center">
                <label className="form-label mb-4" htmlFor="numVuelo"> Numero de vuelo:
                    {/* <input type="text"  name="numVuelo" id="numVuelo"  className="form-control p-2 mt-4"/> */}
                    <input type="text"  name="numVuelo" id="numVuelo"  className="form-control p-2 mt-4" value={numVuelo} onChange={(event) => setNumVuelo(event.target.value)}/>
                </label>
                <div  id="btnSubmit">
                    <button type="submit" className="btn btn-success mb-5" onClick={ handleSubmit }>Buscar</button>
                </div >

                { data ? (
                    <>
                        <MostrarVuelo data={ Object.values(data) }/>
                    </>
                ): null}
        </section>
        </>
    )
}

export default NumeroVuelo;