import React, {useState, useEffect} from 'react'
import '../style.css';
import FormRadio from './FormRadio';
import InputForm from './InputForm';
import axios from '../api/actions/axios';

const Formulario = () => {
    
    const [nacionalidad, setNacionalidad] = useState();
    const [data, setData] = useState();
    const [compra, setCompra] = useState();
    
    //// Parametros
    
    const [codigo, setCodigo]       = useState("");
    const [boleto, setBoleto]       = useState("");
    const [asiento, setAsiento]     = useState("");
    const [vuelo, setVuelo]         = useState("");
    const [nombre, setNombre]       = useState('');
    const [apellidoP, setApellidoP] = useState("");
    const [apellidoM, setApellidoM] = useState("");
    const [telefono, setTelefono]   = useState();
    const [correo, setCorreo]       = useState("");
    const [edad, setEdad]           = useState();
    const [pasaporte, setPasaporte] = useState("");
    const [nac, setNac]             = useState("");
    const [radio, setRadio]         = useState("1");

    const changeCompra = () => {
            let aux = "C" + ((parseInt(compra[0][1].slice(1,3))) + 1)
            console.log(aux)
            setCodigo(aux)
            aux = "A" + ((parseInt(compra[0][1].slice(1,3))) + 1)
            setBoleto(aux)
    }

    useEffect(() => {
        if(!compra){
            axios("compra").then( (res) => {
                setCompra(res.data);
            })
        }
    }, [ compra ])

    useEffect(() => {
        if(!nacionalidad){
            axios("nacionalidad").then( (res) => {
                setNacionalidad(res.data);
            })
        }
    }, [ nacionalidad ])

    const handleSubmit = (e)=> {
        axios("reserva", {
            "codigo": codigo,
            "boleto": boleto,
            "asiento": asiento,
            "vuelo": parseInt(vuelo),
            "nombre": nombre,
            "apellidoP": apellidoP,
            "apellidoM": apellidoM,
            "telefono": telefono,
            "correo": correo,
            "edad": edad,
            "pasaporte": pasaporte,
            "nacionalidad": nac,
            "tipo": parseInt(radio)
        }, "POST").then( (res) => {
            setData(res.data);
        })
        // e.preventDefault();
    }

    console.log(data)

    return nacionalidad && compra ? ( 
        <>
            <h2 className="h2">Reservar un vuelo</h2> <hr/>
            <section className="container">
                <div className="row">
                    <div className="col-md-4">
                        <label htmlFor="inputCompra" className="form-label"> Compra: </label>
                        <input type="text" name="inputCompra" id="inputCompra" className="form-control p2" disabled
                        defaultValue={  codigo }  ></input>
                    </div>
                    <div className="col-md-4">
                        <label htmlFor="inputBoleto" className="form-label"> Boleto: </label>
                        <input type="text" name="inputBoleto" id="inputBoleto" className="form-control p2" disabled
                        defaultValue={ boleto }></input>
                    </div>
                </div>

                <div className="row">
                    <InputForm col="col-md-4" htmlFor="inputName" texto="Nombre(s)" type="text" id="inputName" 
                    defaultValue={ nombre } onChange={ (val) => { setNombre(val); changeCompra() } }  />
                    <InputForm col="col-md-4" htmlFor="inputLastName" texto="Apellido Paterno" type="text" id="inputLastName" defaultValue={ apellidoP } onChange={ (val) => { setApellidoP(val); changeCompra() } } />
                    <InputForm col="col-md-4" htmlFor="inputLastName_2" texto="Apellido Materno" type="text" id="inputLastName_2" defaultValue={ apellidoM } onChange={ (val) => { setApellidoM(val); changeCompra() } } />
                </div>
                
                <div className="row">

                    <div className="input col-md-6 align-self-end">
                        <label htmlFor="selectNacionalidad" className="mb-2">Nacionalidad: </label>
                        <select className="form-select point" id="selectNacionalidad" 
                        defaultValue={ nac } onChange={ (event) => { setNac(event.target.value); changeCompra() } }>
                            <option>Elija una nacionalidad</option>
                            { nacionalidad.map( (data) => ( 
                                <option key={ data[0] } defaultValue={ data[0] }>{ data[1] }</option> 
                            ))
                            }
                        </select>
                    </div>
                    <InputForm col="col-md-3" htmlFor="inputTelefono" texto="Telefono" type="tel" id="inputTelefono" 
                    defaultValue={ telefono } onChange={ (val) => { setTelefono(val); changeCompra() } } />
                    <InputForm col="col-md-3" htmlFor="inputEdad" texto="Edad" type="number" id="inputEdad" 
                    defaultValue={ edad } onChange={ (val) => { setEdad(val); changeCompra() } }/>
                </div>

                <div className="row">
                    <InputForm col="col-md-6" htmlFor="inputCorreo" texto="Correo" type="email" id="inputCorreo" 
                    defaultValue={ correo } onChange={ (val) => { setCorreo(val); changeCompra() } } />
                    <div className="col-md-6 align-self-center">
                        <p className="fs-6 mt-4">Tipo de pasajero:</p>
                        <FormRadio texto="Menor o estudiante" id="radioMenor" value="1" checked={ radio === "1" } onChange={ (val) => { setRadio(val) } }/>
                        <FormRadio texto="Adulto" id="radioAdulto" value="2" checked={ radio === "2" } onChange={ (val) => { setRadio(val) } } />
                        <FormRadio texto="Tercera edad o discapacitado" id="radioTercera" value="3" checked={ radio === "3" } onChange={ (val) => { setRadio(val) } } />
                    </div>
                </div>
                <div className="row">
                    <InputForm col="col-md-3" htmlfor="inputPasaporte" texto="Pasaporte" type="text" id="inputPasaporte" 
                    defaultValue={ pasaporte } onChange={ (val) => { setPasaporte(val); changeCompra() } } />
                    <InputForm col="col-md-3" htmlfor="inputVuelo" texto="Numero de vuelo" type="text" id="inputVuelo" 
                    defaultValue={ vuelo } onChange={ (val) => { setVuelo(val); changeCompra() } } />
                    <InputForm col="col-md-3" htmlfor="inputAsiento" texto="Asiento" type="text" id="inputAsiento" 
                    defaultValue={ asiento } onChange={ (val) => { setAsiento(val); changeCompra() } } />
                </div>
                <br/>
                <div className="col-md-1" id="btnSubmit">
                    <button type="submit" className="btn btn-success mb-5" onClick={ handleSubmit }>Comprar</button>
                </div>
            </section>
        </>
    ) : null
}

export default Formulario;