import React from 'react'
import '../style.css'
import { faPlane, faReceipt ,faServer } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import ItemHeader from './ItemHeader';
import DropDownItem from './DropDownItem';

const Header = () => {
    return (
        <nav className="navbar navbar-expand-lg navbar-light ">
            <div className="container">
                <img className="img-1 img-fluid rounded float-start" src="https://pbs.twimg.com/profile_images/1423918272/esimetwitter_400x400.png" alt="Logo ESIME" width="120" height="96"/>
                <ul className="nav justify-content-center fs-5">
                    <ItemHeader icono={ faReceipt  } texto="RESERVAS" context="/"/>
                    <ItemHeader icono={ faPlane } texto="VUELOS" context="vuelos"/>
                    <li className="nav-item rounded-pill dropdown ">
                    <FontAwesomeIcon icon={ faServer } style={ {color: 'white'}}/>
                        <button type="button" className=" dropdown-toggle"  id="navbarDropdownMenuLink" data-bs-toggle="dropdown" aria-expanded="false"> ADMINISTRADOR</button>
                        <ul className="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                            <DropDownItem texto="DETALLE DE VENTA" context="detalle-compra"/>
                            <DropDownItem texto="VUELOS TOTALES" context="vuelos-totales"/>
                            <DropDownItem texto="VUELOS POR PAIS" context="vuelos-pais"/>
                            <DropDownItem texto="AEROPUERTOS" context="aeropuertos"/>
                            <DropDownItem texto="VENTAS" context="ventas"/>
                        </ul>
                    </li>
                </ul>
            </div>
        </nav>
    )
}

export default Header;