import React, { useState } from 'react'
import axios from '../api/actions/axios';
import '../style.css';
import MostrarVenta from './MostrarVenta';


export const Compra = () => {

    const [data, setData] = useState();
    const [boleto,  setBoleto] = useState('')
    
    const handleSubmit = (e) => {
        axios("detalle-compra", {"boleto": boleto}).then( (res) => {
            if(res.status !== 200) {
                alert(res.data.message);
            } else {
                setData(res.data);
            }
        })
        e.preventDefault();
    }

    console.log(data)
    return (
        <>
            <h2 className="h2">Detalle de una venta:</h2> <hr/>
            <section className="text-center">
                <label className="form-label mb-4" htmlFor="boleto"> Asiento:
                    {/* <input type="text"  name="numVuelo" id="numVuelo"  className="form-control p-2 mt-4"/> */}
                    <input type="text"  name="boleto" id="boleto"  className="form-control p-2 mt-4" value={ boleto } onChange={(event) => setBoleto(event.target.value)}/>
                </label>
                <div  id="btnSubmit">
                    <button type="submit" className="btn btn-success mb-5" onClick={ handleSubmit }>Buscar</button>
                </div >

                { data ? (
                    <>
                        <MostrarVenta data={ Object.values(data) }/>
                    </>
                ): null}
        </section>
        </>
    )
}

export default Compra;