import React from 'react'

const MostrarVenta = ( data ) => {

    const datos = data.data[0].map(e => { return e })

    // const format = ( date ) => { return date.substring(8, 10) + ':' + date.substring(7, 5) + ':' + date.substring(2, 4); }

    return (
        <>
        <hr className="style1"/>
            <h3>
                <ul>
                    <li>Clave de compra: {datos[0]}</li>
                    <li>Boleto: {datos[1]}</li>
                    <li>Asiento: {datos[2]}</li>
                    <li>Precio: ${datos[3]}</li>
                    <li>Numero de vuelo: {datos[4]}</li>
                    <li>Nombre: {datos[5]}</li>
                    <li>Apellido paterno: {datos[6]}</li>
                    <li>Apellido materno: {datos[7]}</li>
                    <li>Telefono: {datos[8]}</li>
                    <li>Correo: {datos[9]}</li>
                    <li>Edad: {datos[10]}</li>
                    <li>Pasaporte: {datos[11]}</li>
                    <li>Nacionalidad: {datos[12]}</li>
                    <li>Tipo de pasajero: {datos[13]}</li>
                </ul>
            </h3>
        </>
    )
}

export default MostrarVenta