import React from 'react'

const MostrarVuelo = ( data ) => {

    const datos = data.data[0].map(e => { return e })

    const format = ( date ) => { return date.substring(8, 10) + ':' + date.substring(7, 5) + ':' + date.substring(2, 4); }

    return (
        <>
        <hr className="style1"/>
            <h3>
                <ul>
                    <li>Numero de vuelo: {datos[0]}</li>
                    <li>Fecha: {datos[1].slice(0, 10)}</li>
                    <li>Hora de abordaje: {format(datos[2].slice(0, 10))}</li>
                    <li>Hora de llegada: {format(datos[3].slice(0, 10))}</li>
                    <li>Destino: {datos[4]}</li>
                    <li>Origen: {datos[5]}</li>
                    <li>Matricula: {datos[6]}</li>
                    <li>Aeropuerto de llegada: {datos[7]}</li>
                </ul>
            </h3>
        </>
    )
}

export default MostrarVuelo
