import React from 'react'

const TodosLosVuelos = ( data ) => {

    const datos = data.data.map(e => { return e })

    const format = ( date ) => { return date.substring(8, 10) + ':' + date.substring(7, 5) + ':' + date.substring(2, 4); }

    return (
        <>
            <h3 className=" text-center">
                <ul >
                    {datos.map(d => { return <li key={d.toString()}> <b> Numero de vuelo: </b> { d[0] } <b>Fecha:</b> { d[1].slice(0, 10) } <br/> <b>Hora de abordaje:</b> { format(d[2]) } --&gt; <b>Hora de llegada:</b> { format(d[3]) } <br/> <b> Destino:</b> {d[4]} <b> Origen:</b> {d[5]} <b> Matricula:</b> {d[6]}</li>  })}
                </ul>
            </h3>
        </>
    )
}

export default TodosLosVuelos