import React, { useState } from 'react'
import axios from '../api/actions/axios';
import '../style.css';

export const DatesVenta = () => {
    
    const [data, setData] = useState();
    const [dateInicial,  setDateInicial] = useState('')
    const [dateFinal, setDateFinal] = useState('')
    
    const handleSubmit = (e) => {
        axios("dates", {"inicial": dateInicial, "final": dateFinal}).then( (res) => {
            if(res.status !== 200) {
                alert(res.data.message);
            } else {
                setData(res.data);
            }
        })
        e.preventDefault();
    }

    console.log(dateInicial)
    console.log(dateFinal)
    return (
        <>
            <h2 className="h2">Ventas por fecha:</h2> <hr/>
            <section className="text-center row section_Ventas container">
                <label className="form-label mb-4 col-md-4" htmlFor="inicial"> Fecha inicial:
                    {/* <input type="text"  name="numVuelo" id="numVuelo"  className="form-control p-2 mt-4"/> */}
                    <input type="date"  name="inicial" id="inicial"  className="form-control p-2 mt-4" defaultValue={ dateInicial } onChange={(event) => setDateInicial(event.target.value)}/>
                </label>
                <label className="form-label mb-4 col-md-4" htmlFor="final"> Fecha final:
                    {/* <input type="text"  name="numVuelo" id="numVuelo"  className="form-control p-2 mt-4"/> */}
                    <input type="date"  name="final" id="final"  className="form-control p-2 mt-4" defaultValue={ dateFinal } onChange={(event) => setDateFinal(event.target.value)}/>
                </label>
                <div  id="btnSubmit">
                    <button type="submit" className="btn btn-success mb-5" onClick={ handleSubmit }>Buscar</button>
                </div >

                { data ? (
                    <h2>Ganancia: ${data[0]} </h2>
                ): null}
        </section>
        </>
    )
}

export default DatesVenta;
