import axios from 'axios';

const ENDPOINT = 'http://localhost:3001'

const call = async (context, params = {}, method="GET") => {
    const url = `${ENDPOINT}/${context}`
    console.log("URL: ", url)
    let options = {
        "method" : method,
        "url": url,
        "headers": getHeaders(),
    }
    switch (method) {
        case "GET": if(params) options = { ...options, params: params }; break;

        case "POST": if(params) options = { ...options, data: params }; break;

        default: ;
    }
    try {
        return await axios(options);
    } catch (err) {
        console.log(err.response)
        return err.response;
    }
}


const getHeaders = () => {
    let headers = {
        "content-type":"application/json; charset=utf-8"
    }
    return headers;
}

export default call;