import React from 'react'

const InputForm = ({ col, htmlfor, texto, type, id, onChange }) => {
    return (
        <div className={ col }>
            <label htmlFor={ htmlfor } className="form-label">{ texto }: </label>
            <input type={ type } name={ id } className="form-control p-2" id={ id } onChange={ (event) => { onChange( event.target.value ) }}/>
        </div>
    )
}

export default InputForm
