import React from 'react'

const MostrarTotalAeropuertos = ( data ) => {

    const datos = data.data.map(e => { return e })

    // const format = ( date ) => { return date.substring(8, 10) + ':' + date.substring(7, 5) + ':' + date.substring(2, 4); }

    console.log(datos)
    return (
        <>
            <h4 className=" text-center">
                <ul >
                    {datos.map(d => { return <li key={d.toString()}>Numero: { d[0] }<br/> Nombre: { d[1] }, Pais: { d[2] },   Estado: { d[3] }<br/> Avenida: { d[4] }, Codigo postal: { d[5] }, Telefono: { d[6] } </li>  })}
                </ul>
            </h4>
        </>
    )
}

export default MostrarTotalAeropuertos